<template>
    <div id="gloBox">
        <div class="box">
            <div class="txal white mg-t-25">视觉欺诈</div>
			<div class="conter">
				<img v-for='item of 16' :key='item' src="/static/img/u=3075879458,1072298874&fm=27&gp=0.jpg"/>
			</div>
		</div>
    </div>
</template>

<script>
    export default {
        created() {
            
        },
    }
</script>

<style lang="less" scoped>
    .box{
        width: 100%;
        min-height: 100px;
        perspective: 1px;
        
    }
    .conter{
        width: 130px;
        height: 130.33px;
        margin: auto;
        top: 100px;
        left: 638px;
        display: inline-block;
        /*line-height: 1180px;*/
        /*transition: transform 1s;*/
        position: relative;
        transform-style: preserve-3d;
        /* transform: rotateX(0deg);
        transform: rotateY(60deg); */
        animation: imgcar 20s cubic-bezier(1, 1, 1, 1) 0s infinite;
    }
    img{
        width: 200px;
        position: absolute;
        top: 15px;
        opacity: .5;
    }
    img:nth-child(1){transform: rotateY(20deg) translateZ(180px);}
    img:nth-child(2){transform: rotateY(40deg) translateZ(180px);}
    img:nth-child(3){transform: rotateY(60deg) translateZ(180px);}
    img:nth-child(4){transform: rotateY(80deg) translateZ(180px);}
    img:nth-child(5){transform: rotateY(100deg) translateZ(180px);}
    img:nth-child(6){transform: rotateY(120deg) translateZ(180px);}
    img:nth-child(7){transform: rotateY(140deg) translateZ(180px);}
    img:nth-child(8){transform: rotateY(160deg) translateZ(180px);}
    img:nth-child(9){transform: rotateY(180deg) translateZ(180px);}
    img:nth-child(10){transform: rotateY(200deg) translateZ(180px);}
    img:nth-child(11){transform: rotateY(240deg) translateZ(180px);}
    img:nth-child(12){transform: rotateY(260deg) translateZ(180px);}
    img:nth-child(13){transform: rotateY(280deg) translateZ(180px);}
    img:nth-child(14){transform: rotateY(300deg) translateZ(180px);}
    img:nth-child(15){transform: rotateY(320deg) translateZ(180px);}
    img:nth-child(16){transform: rotateY(340deg) translateZ(180px);}

    @keyframes imgcar {
        0%{
            transform:rotateY(0deg) rotateX(0deg) rotateX(0deg)
        }
        25%{
            transform:rotateY(360deg) rotateX(0deg) rotateX(0deg)
        }
        50%{
            transform:rotateY(0deg) rotateX(-360deg) rotateX(0deg)
        }
        75%{
            transform:rotateY(0deg) rotateX(0deg) rotateX(360deg)
        }
        100%{
            transform:rotateY(0deg) rotateX(0deg) rotateX(0deg)
        }
    }
</style>


